// ==============================
// Custom style
// 自定义样式
// ==============================

#comments{
    padding-top: 3rem !important; 
}

::-webkit-scrollbar {
    width: 0rem;
    height: 0rem;
}

@media only screen and (max-width: 680px){
[data-header-mobile] .page {
        margin-right: auto !important;
        margin-left: auto !important;
        width: 80%;
    }
}


.toc .toc-content ul{
    text-indent: 0;

}
.footer {
    display: block;
	border-top-width: 0px;
    //border-bottom-width: 0px;
    border-top-style: solid;
    border-top-color: #a166ab;
    position: relative;
    max-width: 800px;
    width: 54.5%;
    margin: .6rem auto 0 auto;
	padding-left: 0rem;
	padding-right: 0rem;
    background: #43434a6f;
	//opacity: 0.85;
    border-top-left-radius: 10px;
    border-top-right-radius: 10px;
    backdrop-filter: blur(15px);
}
[theme=dark] .footer {
    background: #43434a6f;
    backdrop-filter: blur(15px);
}

@media only screen and (max-width: 1440px) {
    .footer {
        width:54.5%
    }
}

@media only screen and (max-width: 1200px) {
    .footer {
        width:60%
    }
}

@media only screen and (max-width: 960px) {
    .footer {
        width: 77%
    }
}

@media only screen and (max-width: 680px) {
    .footer {
        width: 100%
    }
}


/* 目录 */
.toc {
    background: white;
	opacity: .85;
    border-top-left-radius: 8px;
    border-bottom-left-radius : 6px;
    background: #43434a;

}
[theme=dark] .toc {
    background: #43434a;
    opacity: 0.85;
}
[theme=dark] #toc-auto {
    border-left-color: #6f6a6a;
}

#toc-auto {
    border-left-color: #6f6a6a;
}
.toc .toc-content {
    font-size: 1rem;
}

.toc .toc-content code {
    border: none;
    color: #f9785e;
    font-size: 1em;
}

nav#TableOfContents ol {
    padding-inline-start: 20px;
    border-radius: 4px;
    & ol {
        padding-inline-start: 15px;
        font-size: .75rem;
        display: none;
    }

    & li.has-active ol {
        display: block;
    }
}


